<template>
  <div class="search">
    <div class="centerx default-input">
      <vs-input class="inputx" placeholder="Search..." v-model="value" v-on:keyup.enter="search" />
    </div>
    <span @click="search()" class="search-icon">
      <vs-icon icon="search"></vs-icon>
    </span>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      value: ""
    };
  },
  methods: {
    search() {
      let value = this.value;
      if (value.length > 0) {
        this.$router.push({ name: "search", params: { q: value } });
      }
    }
  },
  computed: {
    ...mapGetters({
      storeSearchValue: "search/getSearchValue"
    })
  }
};
</script>

<style lang="scss">
.search {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  .centerx {
    align-items: center;
  }
  span {
    margin-left: 5px;
    display: flex;
    height: 100%;
    cursor: pointer;
    align-items: center;
  }
  .search-icon {
    background: black;
    width: 38px;
    height: 27px;
    display: flex;
    justify-content: center;
    color: #fff;
    border-radius: 5px;
  }
}
</style>